<template>
  <div class="data-set">
    <header>
      <div class="return" @click="goBack()"></div>
      <span class="save"></span>
      <p>{{title}}</p>
    </header>
    <div class="content">
      <p class="title-text">{{content_title}}</p>
      <input type="text" :placeholder="placeholder">
    </div>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/func-components/data-set.scss";
  export default{
    name: "DataSet",
    data(){
      return {
        title: "",
        content_title: "",
        placeholder: "",
        msg: {
          name: "",
          notice: "",
          nickname: ""
        }
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      }
    },
    created: function () {
      if (this.$route.params.type === "1") {
        if (this.$route.params.id === "1") {
          this.title = "";
          this.content_title = "";
          this.placeholder = this.msg.name;
        }
        if (this.$route.params.id === "2") {
          this.title = "";
          this.content_title = "";
          this.placeholder = this.msg.nickname;
        }
        if (this.$route.params.id === "3") {
          this.title = "";
          this.content_title = "";
          this.placeholder = this.msg.notice;
        }
      }
      if (this.$route.params.type === "2") {
        if (this.$route.params.id === "1") {
          this.title = "";
          this.content_title = "";
          this.placeholder = this.msg.nickname;
        }
        if (this.$route.params.id === "2") {
          this.title = "";
          this.content_title = "";
        }
      }

      switch (this.$route.query.id) {
        case "1":
          this.title = "";
          this.content_title = "";
          this.placeholder = this.msg.name;
          break;
        case "2":
          this.title = "";
          this.content_title = "";
          this.placeholder = this.msg.nickname;
          break;
        case "3":
          this.title = "";
          this.content_title = "";
          this.placeholder = this.msg.notice;
          break;
      }
    }
  }
</script>



