<template>
  <div class="location-page">
    <header>
      <div class="return" @click="goBack()"></div>
      <div class="search">
        <div class="icon"></div>
        <input class="pingfang-R" type="text" placeholder="">
      </div>
    </header>
    <ul class="city-location">
      <li class="item">
        <div class="icon"></div>
        <span></span>
        <div class="name"></div>
      </li>
      <li class="item">
        <div class="icon"></div>
        <span></span>
        <div class="name"></div>
        <span class="relocation"></span>
      </li>
    </ul>
    <ul class="city-list">
      <li class="list-item">
        <p class="item-title"></p>
        <ul class="contents">
          <li class="item" v-for="item in msg.recent_visit">{{item}}</li>
        </ul>
      </li>
      <li class="list-item">
        <p class="item-title"></p>
        <ul class="contents">
          <li class="item" v-for="item in msg.hot_city">{{item}}</li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  import router from "../../router/index"
  import "@/assets/style/home-page/location-page.scss";
  export default{
    name: "LocationPage",
    data(){
      return {
        msg: {
          current_city: "",
          loc_city: "",
          recent_visit: ['', '', ''],
          hot_city: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
        }
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      }
    }
  }
</script>



