<template>
  <div class="payment">
    <header>
      <div class="return" @click="goBack()"></div>
      <p></p>
    </header>
    <div class="payment-info clearfix">
      <div class="icon"></div>
      <div class="payment-content">
        <div class="amount clearfix">
          <span></span>
          <span class="amount-content">2.5</span>
        </div>
        <div class="payee clearfix">
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
    <ul class="payment-methods">
      <li class="item" :class="{'active':active==1}" @click="setActive(1)">
        <div class="icon"></div>
        <div class="method-info">
          <p class="name"></p>
          <p class="prompt"></p>
        </div>
        <div class="select"></div>
      </li>
      <li class="item" :class="{'active':active==2}" @click="setActive(2)">
        <div class="icon"></div>
        <div class="method-info">
          <p class="name"></p>
          <p class="prompt">5.0</p>
        </div>
        <div class="select"></div>
      </li>
      <li class="item" :class="{'active':active==3}" @click="setActive(3)">
        <div class="icon"></div>
        <div class="method-info">
          <p class="name"></p>
          <p class="prompt"></p>
        </div>
        <div class="select"></div>
      </li>
    </ul>
    <div class="submit">
      <span></span>
      <span></span>
      <span>2.5</span>
    </div>
  </div>
</template>

<script>
  import router from "@/router/index"
  import "@/assets/style/func-components/payment.scss";
  export default{
    name: "Payment",
    data(){
      return {
        active: 0
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      },
      setActive: function (id) {
        this.active = id;
      }
    }
  }
</script>



