<template>
  <div class="id-authenticate">
    <header>
      <div class="return" @click="goBack()"></div>
      <p></p>
    </header>
    <div class="content content-false" v-show="!status">
      <p class="hint-text"></p>
      <div class="hint-pic">
        <p></p>
        <div class="pic">
          <div></div>
        </div>
      </div>
    </div>
    <div class="content content-true" v-show="status">
      <div class="hint">
        <div class="icon"></div>
        <span></span>
      </div>
      <div class="data">
        <p>{{msg.name}}</p>
        <p>{{msg.id_number}}</p>
      </div>
    </div>
    <div class="submitButton-bottom" v-show="!status"></div>
    <div class="submitButton-bottom" v-show="status" @click="goBack()"></div>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/personal-center/id-authenticate.scss";
  export default{
    name: "IdAuthenticate",
    data(){
      return {
        status: false,
        msg: {
          name: "",
          id_number: 322635897522366582
        }
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      }
    }
  }
</script>



