<template>
  <div class="kid-data">
    <header>
      <div class="return" @click="goBack()"></div>
      <p></p>
    </header>
    <ul class="content">
      <li class="item">
        <span class="item-name"></span>
        <div class="item-content">
          <img :src="msg.pic" alt="" class="pic">
          <!--<span></span>-->
          <div class="come-in"></div>
          <div class="mask" @click="setStatus(1)"></div>
        </div>
      </li>
      <li class="item">
        <span class="item-name"></span>
        <div class="item-content">
          <span></span>
          <div class="come-in"></div>
          <router-link to="/id-authenticate" class="mask"></router-link>
        </div>
      </li>
      <li class="item">
        <span class="item-name"></span>
        <div class="item-content">
          <span>{{this.msg.gender || ""}}</span>
          <div class="come-in"></div>
          <div class="mask" @click="setStatus(2)"></div>
        </div>
      </li>
      <li class="item">
        <span class="item-name"></span>
        <div class="item-content">
          <span></span>
          <div class="come-in"></div>
          <div class="mask" @click="setStatus(true)"></div>
        </div>
      </li>
      <li class="item">
        <span class="item-name"></span>
        <div class="item-content">
          <span>{{this.msg.relationship || ""}}</span>
          <div class="come-in"></div>
          <div class="mask" @click="setStatus(3)"></div>
        </div>
      </li>
    </ul>
    <!---->
    <div class="pic-set set-one" v-show="status===1">
      <div class="shadow" @click="setStatus(false)"></div>
      <div class="section-bottom">
        <p></p>
        <p></p>
        <p @click="setStatus(false)"></p>
      </div>
    </div>
    <!---->
    <div class="gender-set set-one" v-show="status===2">
      <div class="shadow" @click="setStatus(false)"></div>
      <div class="section-bottom">
        <p @click="setGender('')"></p>
        <p @click="setGender('')"></p>
        <p @click="setStatus(false)"></p>
      </div>
    </div>
    <!---->
    <div class="gender-set set-one" v-show="status===3">
      <div class="shadow" @click="setStatus(false)"></div>
      <div class="section-bottom">
        <p @click="setRelationship('')"></p>
        <p @click="setRelationship('')"></p>
        <ul class="more" v-show="!status_relation" @click="relationMore()">
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <p @click="setRelationship('')" v-show="status_relation"></p>
        <p @click="setRelationship('')" v-show="status_relation"></p>
        <p @click="setRelationship('')" v-show="status_relation"></p>
        <p @click="setRelationship('')" v-show="status_relation"></p>
        <p @click="setRelationship('')" v-show="status_relation"></p>
        <p @click="setStatus(false)"></p>
      </div>
    </div>

    <div class="submitButton-bottom"></div>
  </div>
</template>

<script>
  import router from "@/router/index";
  import "../../assets/style/personal-center/add-kidArchive.scss";
  export default{
    data(){
      return {
        msg: {
          id: 0,
          name: "",
          gender: "",
          birthday: "",
          relationship: "",
          status: true,
          pic: "/static/images/personal-center/my_pic.png",
        },
        status: false,
        status_relation: false
      }
    },
    methods: {
      goBack: function () {
        router.go(-1);
      },
      setStatus: function (para) {
        this.status = para;
        this.status_relation = false;
      },
      setGender: function (para) {
        this.msg.gender = para;
        this.status = false;
      },
      setRelationship: function (para) {
        this.msg.relationship = para;
        this.status = false;
        this.status_relation = false;
      },
      relationMore: function () {
        this.status_relation = true;
      }
    }
  }
</script>



